import React, { Component, useState } from 'react';
import { StaticQuery, graphql } from "gatsby";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { Modal, Button, Tab, Nav, Accordion } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import LocationMapImage from "../images/Location.jpg";
import LocationIcon from "../images/maplocation.webp";
import parse from 'html-react-parser';
import $ from "jquery";


export default function Location() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const fnSendMeDetail = (form,pid,pname) => {
    var name = $("#" + form + " input[name=name]").val();
    var email = $("#" + form + " input[name=email]").val();
    var mobile_no = $("#" + form + " input[name=mobile]").val();

    var email_pattern = /^\w+([-+.'][^\s]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
    var mobile_length = mobile_no.length;

    if (name == "") {
      $("#" + form + " .form_error").html("Please enter name");
      return false;
    } else {
      $("#" + form + " .form_error").html("");
    }

    if (email != "") {
      if (!email_pattern.test(email)) {
        $("#" + form + " .form_error").html("Please enter valid email");
        return false;
      } else {
        $("#" + form + " .form_error").html("");
      }
    }

    if (mobile_no == "") {
      $("#" + form + " .form_error").html("Please enter mobile no.");
      return false;
    } else if (mobile_length != 10) {
      $("#" + form + " .form_error").html("Please enter valid mobile no.");
      return false;
    } else {
      $("#" + form + " .form_error").html("");
    }

    $('#' + form + ' button').prop('disabled', true);

    var lead = {
      name: name,
      email: email,
      mobile: mobile_no,
      project: pname,
      project_id: pid
    };

    $.ajax({
      type: "post",
      url: 'https://mls-crm.manishalifespaces.com/api/add_lead',
      data: lead,
      success: function (data, status, jqXHR) {
        window.location.href = 'thanks';
      },
      error: function (jqXHR, status) {
        $('#' + form + ' button').prop('disabled', false);
      }
    });
  }
  return (
    <StaticQuery
      query={graphql`
        query Location {
          allWpPage(filter: {slug: {eq: "location-map"}}) {
            nodes {
              content
            }
          }
          connectivity: allWpPost(filter: {uri: {}, slug: {eq: "connectivity"}}) {
            nodes {
              content
            }
          }
          infrastructure: allWpPost(filter: {uri: {}, slug: {eq: "infrastructure"}}) {
            nodes {
              content
            }
          }
          neighborhood: allWpPost(filter: {uri: {}, slug: {eq: "neighborhood"}}) {
            nodes {
              content
            }
          }
          wellness: allWpPost(filter: {uri: {}, slug: {eq: "wellness"}}) {
            nodes {
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          shopping: allWpPost(filter: {uri: {}, slug: {eq: "shopping"}}) {
            nodes {
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          education: allWpPost(filter: {uri: {}, slug: {eq: "education"}}) {
            nodes {
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          commercial: allWpPost(filter: {uri: {}, slug: {eq: "commercial"}}) {
            nodes {
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          logo: allWpPost(filter: {uri: {}, slug: {eq: "logo"}}) {
            nodes {
              slug
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          popupLogo:allWpPost(filter: {uri: {}, slug: {eq: "popup-logo"}}) {
            nodes {
              slug
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          popupBgImg:allWpPost(filter: {uri: {}, slug: {eq: "popup-bg-image"}}) {
            nodes {
              slug
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          ProjectId:allWpPost(filter: {uri: {}, slug: {eq: "project-id"}}) {
            nodes {
              content
            }
          }
          ProjectName:allWpPost(filter: {uri: {}, slug: {eq: "project-name"}}) {
            nodes {
              content
            }
          }
          primaryColor: allWpPost(filter: {uri: {}, slug: {eq: "primary-color"}}) {
            nodes {
              content
            }
          }
          secondaryColor: allWpPost(filter: {uri: {}, slug: {eq: "secondary-color"}}) {
            nodes {
              content
            }
          }
        }
      `}

      render={data => (

        <><section id="location">
          <div class="container">
            <div class="box">
              <div class="header-title">
                <h2 class="text-center" style={{ color: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}>LOCATION</h2>
                <div class="underline" style={{ borderBottom: `4px solid ${data.secondaryColor.nodes[0].content.replace('<p>','').replace('</p>','')}` }}></div>
              </div>
              <div class="row">
              <Tab.Container id="gallery-tabs" defaultActiveKey="1">
            <Nav variant="pills" className="flex-row">
              <Nav.Item>
                <Nav.Link eventKey="1" href="javascript:void(0);">
                Location Map
                </Nav.Link>
                </Nav.Item>
                <Nav.Item>
                <Nav.Link eventKey="2" href="javascript:void(0);">
                Connectivity
                </Nav.Link>
                </Nav.Item>
                <Nav.Item>
                <Nav.Link eventKey="3" href="javascript:void(0);">
                Location Advantages
                </Nav.Link>
              </Nav.Item>
            </Nav>
            <Tab.Content>
            <Tab.Pane eventKey="1">
            <div class="map-container">
                      <div
                        style={{ height: "400px", width: "100%", display: "inline-block", overflow: "hidden", marginBottom: "-20px" }}>
                        <div id="LocationMapContent" class=""> <iframe class="custom-google-map"
                          style={{ position: "relative", top: "-130px", border: "none" }}
                          src={data.allWpPage.nodes[0].content.replace(/<\/?[^>]+(>|$)/g, "")}
                          width="100%" height="500px"></iframe>
                        </div>
                      </div>
                    </div>
            </Tab.Pane>
            <Tab.Pane eventKey="2">
            <div class="row">
                      <div class="col-md-6"> <a data-fancybox href="assets/img/location map.jpeg"> <img
                        src={LocationMapImage} class="w-100" alt="JP Dream Homes" />
                      </a> </div>
                      <div class="col-md-6">
                      <Accordion defaultActiveKey="0">
                        <Accordion.Item eventKey="0">
                          <Accordion.Header>Connectivity</Accordion.Header>
                          <Accordion.Body>
                          {parse(data.connectivity.nodes[0].content)}
                          </Accordion.Body>
                        </Accordion.Item>
                        <Accordion.Item eventKey="1">
                          <Accordion.Header>Infrastructure</Accordion.Header>
                          <Accordion.Body>
                          {parse(data.infrastructure.nodes[0].content)}
                          </Accordion.Body>
                        </Accordion.Item>
                        <Accordion.Item eventKey="2">
                          <Accordion.Header>Neighborhood</Accordion.Header>
                          <Accordion.Body>
                          {parse(data.neighborhood.nodes[0].content)}
                          </Accordion.Body>
                        </Accordion.Item>
                      </Accordion>
                      </div>
                    </div>
            </Tab.Pane>
            <Tab.Pane eventKey="3">
            <Slider
                      initialSlide={4}
                      autoplay={true}
                      autoplaySpeed={3000}
                      dots={false}
                      infinite={true}
                      speed={500}
                      slidesToShow={4}
                      slidesToScroll={1}
                      responsive= {[
                        {
                          breakpoint: 1024,
                          settings: {
                            slidesToShow: 2,
                            slidesToScroll: 2,
                            initialSlide:2
                          }
                        },
                        {
                          breakpoint: 600,
                          settings: {
                            slidesToShow: 1,
                            slidesToScroll: 1,
                            initialSlide:1
                          }
                        }
                      ]}>
                      <div class="item">
                        <div class="card">
                          <div class="location-image"
                            style={{ backgroundImage: `url( ${data.wellness.nodes[0].featuredImage.node.sourceUrl})` }}></div>
                          <h5 style={{ backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}>Wellness</h5>
                          {parse(data.wellness.nodes[0].content)}
                        </div>
                      </div>
                      <div class="item">
                        <div class="card">
                          <div class="location-image"
                            style={{ backgroundImage: `url( ${data.shopping.nodes[0].featuredImage.node.sourceUrl})` }}></div>
                          <h5 style={{ backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}>Shopping</h5>
                          {parse(data.shopping.nodes[0].content)}
                        </div>
                      </div>
                      <div class="item">
                        <div class="card">
                          <div class="location-image"
                            style={{ backgroundImage: `url( ${data.education.nodes[0].featuredImage.node.sourceUrl})` }}></div>
                          <h5 style={{ backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}>Education</h5>
                          {parse(data.education.nodes[0].content)}
                        </div>
                      </div>
                      <div class="item">
                        <div class="card">
                          <div class="location-image"
                            style={{ backgroundImage: `url( ${data.commercial.nodes[0].featuredImage.node.sourceUrl})` }}></div>
                          <h5 style={{ backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}>Commercial</h5>
                          {parse(data.commercial.nodes[0].content)}
                        </div>
                      </div>
                    </Slider>
            </Tab.Pane>
              </Tab.Content>
              </Tab.Container>
              <div class="text-center mt-5"> <button onClick={handleShow}
                    class="btn btn-primary" style={{ backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}><img src={LocationIcon} />  Get Location on your phone</button> </div>
              </div>
            </div>
          </div>
        </section><Modal show={show} onHide={handleClose} centered>
        <Modal.Body style={{ backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}>
            <Button variant="primary" onClick={handleClose} style={{ float: 'right', backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>',''), border: `1px solid ${data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','')}` }}>X</Button>
              <div class="popup-bg" style={{ backgroundImage: `url( ${data.popupBgImg.nodes[0].featuredImage.node.sourceUrl})` }}>
                <div style={{ backgroundColor: 'rgba(0, 0, 0, 0.61)' }}>
                <img style={{ width: "100%", padding: "4rem 6rem", margin: "auto" }} src={data.popupLogo.nodes[0].featuredImage.node.sourceUrl} />
                </div>
              </div>
              <h4 class="text-center text-white pt-5 pb-4">Fill details for current offers</h4>
              <form id="locationPopupForm" class="px-4">
                <div class="mb-3"> <input type="text" class="form-control" name="name" placeholder="Name" /> </div>
                <div class="mb-3"> <input type="email" class="form-control" name="email" placeholder="Email" /> </div>
                <div class="mb-3"> <input type="number" class="form-control mobile" name="mobile" placeholder="Mobile No." /> </div>
                {/* <div class="form-check mb-3"> <input class="form-check-input" type="checkbox" value="" id="location-form-check" /> <label class="form-check-label text-white" for="location-form-check"> Yes, I want quick call back </label> </div> */}
                <p class="text-danger form_error"></p><button type="button" class="btn btn-primary shadow-lg w-100 mb-5" onClick={() => fnSendMeDetail("locationPopupForm",data.ProjectId.nodes[0].content.replace(/<\/?[^>]+(>|$)/g, ""),data.ProjectName.nodes[0].content.replace(/<\/?[^>]+(>|$)/g, ""))} style={{ backgroundColor: data.secondaryColor.nodes[0].content.replace('<p>','').replace('</p>',''), border: `1px solid ${data.secondaryColor.nodes[0].content.replace('<p>','').replace('</p>','')}` }}>SEND ME DETAILS</button> </form>
            </Modal.Body>
          </Modal></>

      )}
    />
  )
}