import React, { Component } from 'react';
import { StaticQuery, graphql } from "gatsby";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export default function AboutDeveloper() {
  // const projects = data.otherProjects.edges;
  // let otherProj = data;
  const displayData = (data) => {
    
    let otherProj = data.otherProjects.edges;

    otherProj.forEach((project, index) => (
      console.log(project.node.toString())
    ))
    console.log(otherProj)
  }

  return (
    <StaticQuery
      query={graphql`
        query aboutDeveloper {
          aboutDeveloper: allWpPage(filter: {slug: {eq: "about-developer"}}) {
            nodes {
              content
            }
          }
          otherProjects: allWpPost(filter: {terms: {nodes: {elemMatch: {slug: {eq: "projects"}}}}}) {
            edges {
              node {
                id
                title
                featuredImage {
                  node {
                    sourceUrl
                  }
                }
              }
            }
          }
          primaryColor: allWpPost(filter: {uri: {}, slug: {eq: "primary-color"}}) {
            nodes {
              content
            }
          }
          secondaryColor: allWpPost(filter: {uri: {}, slug: {eq: "secondary-color"}}) {
            nodes {
              content
            }
          }
        }
      `}

      render={data => (
        <section>
          <div class="container">
            <div class="row">
              <div class="col-lg-5">
                <div class="header-title">
                  <h2 class="text-center" style={{ color: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}>OTHER PROJECTS </h2>
                  <div class="underline"></div>
                </div>
                <Slider
                  autoplay={true}
                  autoplaySpeed={3000}
                  dots={false} 
                  infinite={true} 
                  speed={500} 
                  slidesToShow={1} 
                  slidesToScroll={1}>
                  {data.otherProjects.edges.map((project, index) => (
                    <div>
                      <div class="slider-image" style={{ backgroundImage: `url( ${project.node.featuredImage.node.sourceUrl})`, height: '50vh' }} >
                        <div class="developer_title">
                          <h5 class='mb-0'>{project.node.title}</h5>
                        </div>
                      </div>
                    </div>
                  ))}
                </Slider>
              </div>
              <div class="col-lg-7">
                <div class="header-title mt-lg-0 mt-5">
                  <h2 class="text-center" style={{ color: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}>ABOUT DEVELOPER</h2>
                  <div class="underline" style={{ borderBottom: `4px solid ${data.secondaryColor.nodes[0].content.replace('<p>','').replace('</p>','')}` }}></div>
                </div>
                <p class="developer-para"><div dangerouslySetInnerHTML={{ __html: data.aboutDeveloper.nodes[0].content }} /></p>
              </div>
            </div>
          </div>
        </section>

      )}
    />
  )
}