import React, { Component, useState } from 'react';
import { StaticQuery, graphql } from "gatsby";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { Modal, Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import $ from "jquery";

export default function Ctaoffer() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const fnSendMeDetail = (form, pid, pname) => {
    var name = $("#" + form + " input[name=name]").val();
    var email = $("#" + form + " input[name=email]").val();
    var mobile_no = $("#" + form + " input[name=mobile]").val();

    var email_pattern = /^\w+([-+.'][^\s]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
    var mobile_length = mobile_no.length;

    if (name == "") {
      $("#" + form + " .form_error").html("Please enter name");
      return false;
    } else {
      $("#" + form + " .form_error").html("");
    }

    if (email != "") {
      if (!email_pattern.test(email)) {
        $("#" + form + " .form_error").html("Please enter valid email");
        return false;
      } else {
        $("#" + form + " .form_error").html("");
      }
    }

    if (mobile_no == "") {
      $("#" + form + " .form_error").html("Please enter mobile no.");
      return false;
    } else if (mobile_length != 10) {
      $("#" + form + " .form_error").html("Please enter valid mobile no.");
      return false;
    } else {
      $("#" + form + " .form_error").html("");
    }

    $('#' + form + ' button').prop('disabled', true);

    var lead = {
      name: name,
      email: email,
      mobile: mobile_no,
      project: pname,
      project_id: pid
    };

    $.ajax({
      type: "post",
      url: 'https://mls-crm.manishalifespaces.com/api/add_lead',
      data: lead,
      success: function (data, status, jqXHR) {
        window.location.href = 'thanks';
      },
      error: function (jqXHR, status) {
        $('#' + form + ' button').prop('disabled', false);
      }
    });
  }
  return (
    <StaticQuery
      query={graphql`
      query slider {
        topoffer: allWpPost(filter: {slug: {eq: "top-offer"}}) {
           edges {
             node {
                  id
                content
          }
        }
      }
      topoffer2: allWpPost(filter: {slug: {eq: "top-offer-2"}}) {
        edges {
          node {
               id
             content
       }
     }
   }
      slider: allWpPost(filter: {terms: {nodes: {elemMatch: {slug: {eq: "banners"}}}}}) {
            edges {
              node {
                id
                title
                featuredImage {
                  node {
                    sourceUrl
                  }
                }
              }
            }
          }
          logo: allWpPost(filter: {uri: {}, slug: {eq: "logo"}}) {
            nodes {
              slug
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          popupLogo:allWpPost(filter: {uri: {}, slug: {eq: "popup-logo"}}) {
            nodes {
              slug
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          popupBgImg:allWpPost(filter: {uri: {}, slug: {eq: "popup-bg-image"}}) {
            nodes {
              slug
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          ProjectId:allWpPost(filter: {uri: {}, slug: {eq: "project-id"}}) {
            nodes {
              content
            }
          }
          ProjectName:allWpPost(filter: {uri: {}, slug: {eq: "project-name"}}) {
            nodes {
              content
            }
          }
          primaryColor: allWpPost(filter: {uri: {}, slug: {eq: "primary-color"}}) {
            nodes {
              content
            }
          }
          secondaryColor: allWpPost(filter: {uri: {}, slug: {eq: "secondary-color"}}) {
            nodes {
              content
            }
          }
    } 
      
      `}
      render={data => (
        <><div id="slider">
          <Slider
            autoplay={true}
            autoplaySpeed={3000}
            dots={false}
            infinite={true}
            speed={500}
            slidesToShow={1}
            slidesToScroll={1}>
            {data.slider.edges.map((slide, index) => (
              <div>
                <div class="slider-image d-none d-xl-block" style={{ backgroundImage: `url( ${slide.node.featuredImage.node.sourceUrl})` }}> </div>
                <img class="w-100 d-block d-xl-none" src={slide.node.featuredImage.node.sourceUrl} alt={slide.node.title} />
              </div>
            ))}
          </Slider>
          <div class="offer">
            <div class="top-offer">
              <div dangerouslySetInnerHTML={{ __html: data.topoffer.edges[0].node.content }} />
              <button onClick={handleShow} class="btn" style={{ backgroundColor: '#99780b', color: '#fff', marginBottom: '10px' }}>Enquiry Now</button>
              <div dangerouslySetInnerHTML={{ __html: data.topoffer2.edges[0].node.content }} />
              {/* {data.topoffer.edges[0].node.content} */}
            </div>
          </div>
          <div class="main-form" style={{ backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}>
            <h4 class="text-center text-white pt-5 pb-4">Fill details for current offers</h4>
            <form id="mainForm" class="px-4">
              <div class="mb-3"> <input type="text" class="form-control" name="name" placeholder="Name" /> </div>
              <div class="mb-3"> <input type="email" class="form-control" name="email" placeholder="Email" /> </div>
              <div class="mb-3"> <input type="number" class="form-control mobile" name="mobile" placeholder="Mobile No." /> </div>
              {/* <div class="form-check mb-3"> <input class="form-check-input" type="checkbox" value="" id="main-form-check" /> <label class="form-check-label text-white" for="main-form-check"> Yes, I want quick call back </label> </div> */}
              <p class="text-danger form_error"></p><button type="button" class="btn btn-primary shadow-lg w-100 mb-5" style={{ backgroundColor: data.secondaryColor.nodes[0].content.replace('<p>','').replace('</p>',''), border: `1px solid ${data.secondaryColor.nodes[0].content.replace('<p>','').replace('</p>','')}` }} onClick={() => fnSendMeDetail("mainForm", data.ProjectId.nodes[0].content.replace(/<\/?[^>]+(>|$)/g, ""), data.ProjectName.nodes[0].content.replace(/<\/?[^>]+(>|$)/g, ""))}>SEND ME DETAILS</button> </form>
          </div>
        </div><Modal show={show} onHide={handleClose} centered>
            <Modal.Body style={{ backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}>
            <Button variant="primary" onClick={handleClose} style={{ float: 'right', backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>',''), border: `1px solid ${data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','')}` }}>X</Button>
              <div class="popup-bg" style={{ backgroundImage: `url( ${data.popupBgImg.nodes[0].featuredImage.node.sourceUrl})` }}>
                <div style={{ backgroundColor: 'rgba(0, 0, 0, 0.61)' }}>
                  <img style={{ width: "100%", padding: "4rem 6rem", margin: "auto" }} src={data.popupLogo.nodes[0].featuredImage.node.sourceUrl} />
                </div>
              </div>
              <h4 class="text-center text-white pt-5 pb-4">Fill details for current offers</h4>
              <form id="offerPopupForm" class="px-4">
                <div class="mb-3"> <input type="text" class="form-control" name="name" placeholder="Name" /> </div>
                <div class="mb-3"> <input type="email" class="form-control" name="email" placeholder="Email" /> </div>
                <div class="mb-3"> <input type="number" class="form-control mobile" name="mobile" placeholder="Mobile No." /> </div>
                {/* <div class="form-check mb-3"> <input class="form-check-input" type="checkbox" value="" id="main-form-check" /> <label class="form-check-label text-white" for="main-form-check"> Yes, I want quick call back </label> </div> */}
                <p class="text-danger form_error"></p><button type="button" class="btn btn-primary shadow-lg w-100 mb-5" style={{ backgroundColor: data.secondaryColor.nodes[0].content.replace('<p>','').replace('</p>',''), border: `1px solid ${data.secondaryColor.nodes[0].content.replace('<p>','').replace('</p>','')}` }} onClick={() => fnSendMeDetail("offerPopupForm", data.ProjectId.nodes[0].content.replace(/<\/?[^>]+(>|$)/g, ""), data.ProjectName.nodes[0].content.replace(/<\/?[^>]+(>|$)/g, ""))}>SEND ME DETAILS</button> </form>
            </Modal.Body>
          </Modal></>

      )}
    />
  )
}
