import React, { Component, useState } from 'react';
import { StaticQuery, graphql } from "gatsby";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Costing from "../images/costing-details.jpeg";
import { Modal, Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import $ from "jquery";

export default function Price() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const fnSendMeDetail = (form,pid,pname) => {
    var name = $("#" + form + " input[name=name]").val();
    var email = $("#" + form + " input[name=email]").val();
    var mobile_no = $("#" + form + " input[name=mobile]").val();

    var email_pattern = /^\w+([-+.'][^\s]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
    var mobile_length = mobile_no.length;

    if (name == "") {
      $("#" + form + " .form_error").html("Please enter name");
      return false;
    } else {
      $("#" + form + " .form_error").html("");
    }

    if (email != "") {
      if (!email_pattern.test(email)) {
        $("#" + form + " .form_error").html("Please enter valid email");
        return false;
      } else {
        $("#" + form + " .form_error").html("");
      }
    }

    if (mobile_no == "") {
      $("#" + form + " .form_error").html("Please enter mobile no.");
      return false;
    } else if (mobile_length != 10) {
      $("#" + form + " .form_error").html("Please enter valid mobile no.");
      return false;
    } else {
      $("#" + form + " .form_error").html("");
    }

    $('#' + form + ' button').prop('disabled', true);

    var lead = {
      name: name,
      email: email,
      mobile: mobile_no,
      project: pname,
      project_id: pid
    };

    $.ajax({
      type: "post",
      url: 'https://mls-crm.manishalifespaces.com/api/add_lead',
      data: lead,
      success: function (data, status, jqXHR) {
        window.location.href = 'thanks';
      },
      error: function (jqXHR, status) {
        $('#' + form + ' button').prop('disabled', false);
      }
    });
  }
  return (
    <StaticQuery
      query={graphql`
        query Price {
          allWpPost(filter: {terms: {nodes: {elemMatch: {slug: {eq: "price"}}}}}) {
            edges {
              node {
                title
                content
              }
            }
          }
          logo: allWpPost(filter: {uri: {}, slug: {eq: "logo"}}) {
            nodes {
              slug
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          popupLogo:allWpPost(filter: {uri: {}, slug: {eq: "popup-logo"}}) {
            nodes {
              slug
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          popupBgImg:allWpPost(filter: {uri: {}, slug: {eq: "popup-bg-image"}}) {
            nodes {
              slug
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          ProjectId:allWpPost(filter: {uri: {}, slug: {eq: "project-id"}}) {
            nodes {
              content
            }
          }
          ProjectName:allWpPost(filter: {uri: {}, slug: {eq: "project-name"}}) {
            nodes {
              content
            }
          }
          primaryColor: allWpPost(filter: {uri: {}, slug: {eq: "primary-color"}}) {
            nodes {
              content
            }
          }
          secondaryColor: allWpPost(filter: {uri: {}, slug: {eq: "secondary-color"}}) {
            nodes {
              content
            }
          }
        }
      `}
      render={data => (

        <><section id="price">
          <div class="container">
            <div class="header-title">
              <h2 class="text-center" style={{ color: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}>price list</h2>
              <div class="underline" style={{ borderBottom: `4px solid ${data.secondaryColor.nodes[0].content.replace('<p>','').replace('</p>','')}` }}></div>
              <Slider
                dots={false}
                infinite={false}
                speed={500}
                slidesToShow={4}
                slidesToScroll={1}
                responsive= {[
                  {
                    breakpoint: 1024,
                    settings: {
                      slidesToShow: 2,
                      slidesToScroll: 2,
                      initialSlide:2
                    }
                  },
                  {
                    breakpoint: 600,
                    settings: {
                      slidesToShow: 1,
                      slidesToScroll: 1,
                      initialSlide:1
                    }
                  }
                ]}>
                <div class="item">
                  <div class="card bg-transparent">
                    <div class="price-image" style={{ backgroundImage: `url( ${Costing})` }}>
                      <div class="text-center"><button type="button"
                        class="btn btn-primary shadow-lg position-relative" style={{ top: "12rem",  backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}
                        onClick={handleShow}>Get Cost
                        Sheet</button></div>
                    </div>
                  </div>
                </div>
                {data.allWpPost.edges.map((price, index) => (
                  <div class="item">
                    <div class="card bg-transparent">
                      <div class="price-image px-5">
                        <div class="text-center"><button type="button"
                          class="btn btn-primary shadow-lg position-relative" style={{ top: "12rem",  backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}
                          onClick={handleShow}>Get Cost
                          Sheet</button></div>
                        <h5>{price.node.title}</h5>
                        <div dangerouslySetInnerHTML={{ __html: price.node.content }}></div>
                      </div>
                    </div>
                  </div>
                ))}
              </Slider>
            </div>
          </div>
        </section><Modal show={show} onHide={handleClose} centered>
        <Modal.Body style={{ backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}>
            <Button variant="primary" onClick={handleClose} style={{ float: 'right', backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>',''), border: `1px solid ${data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','')}` }}>X</Button>
              <div class="popup-bg" style={{ backgroundImage: `url( ${data.popupBgImg.nodes[0].featuredImage.node.sourceUrl})` }}>
                <div style={{ backgroundColor: 'rgba(0, 0, 0, 0.61)' }}>
                <img style={{ width: "100%", padding: "4rem 6rem", margin: "auto" }} src={data.popupLogo.nodes[0].featuredImage.node.sourceUrl} />
                </div>
              </div>
              <h4 class="text-center text-white pt-5 pb-4">Fill details for current offers</h4>
              <form id="pricePopupForm" class="px-4">
                <div class="mb-3"> <input type="text" class="form-control" name="name" placeholder="Name" /> </div>
                <div class="mb-3"> <input type="email" class="form-control" name="email" placeholder="Email" /> </div>
                <div class="mb-3"> <input type="number" class="form-control mobile" name="mobile" placeholder="Mobile No." /> </div>
                {/* <div class="form-check mb-3"> <input class="form-check-input" type="checkbox" value="" id="price-form-check" /> <label class="form-check-label text-white" for="price-form-check"> Yes, I want quick call back </label> </div> */}
                <p class="text-danger form_error"></p><button type="button" class="btn btn-primary shadow-lg w-100 mb-5" onClick={() => fnSendMeDetail("pricePopupForm",data.ProjectId.nodes[0].content.replace(/<\/?[^>]+(>|$)/g, ""),data.ProjectName.nodes[0].content.replace(/<\/?[^>]+(>|$)/g, ""))} style={{ backgroundColor: data.secondaryColor.nodes[0].content.replace('<p>','').replace('</p>',''), border: `1px solid ${data.secondaryColor.nodes[0].content.replace('<p>','').replace('</p>','')}` }}>SEND ME DETAILS</button> </form>
            </Modal.Body>
          </Modal></>

      )}
    />
  )
}