import React, { Component } from 'react'
import { StaticQuery, graphql } from "gatsby"

export default function Highlights() {
  return (
    <StaticQuery
      query={graphql`
        query Highlights {
          allWpPage(filter: {slug: {eq: "highlights"}}) {
            nodes {
              slug
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          primaryColor: allWpPost(filter: {uri: {}, slug: {eq: "primary-color"}}) {
            nodes {
              content
            }
          }
          secondaryColor: allWpPost(filter: {uri: {}, slug: {eq: "secondary-color"}}) {
            nodes {
              content
            }
          }
          sectionBg: allWpPost(filter: {uri: {}, slug: {eq: "section-bg"}}) {
            nodes {
              content
            }
          }
        }
      `}
      render={data => (

        <section id="highlights" class="bg" style={{ backgroundColor: data.sectionBg.nodes[0].content.replace('<p>','').replace('</p>','') }}>
          <div class="container">
            <div class="box">
              <div class="header-title">
                <h2 class="text-center" style={{ color: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}>Highlights</h2>
                <div class="underline" style={{ borderBottom: `4px solid ${data.secondaryColor.nodes[0].content.replace('<p>','').replace('</p>','')}` }}></div>
              </div>
              <div class="row">
                <div class="col-lg-6">
                  <div class="content"> <img src={data.allWpPage.nodes[0].featuredImage.node.sourceUrl} class="img-responsive resp2" style={{ width: "100%", height: "auto" }} /></div>
                </div>
                <div class="col-lg-6">
                  <div class="content">
                    <div class="heighlight-points">
                      <table class="table table-bordered">
                        <tbody dangerouslySetInnerHTML={{ __html: data.allWpPage.nodes[0].content }} ></tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      )}
    />
  )
}