import React, { Component, useState } from 'react';
import { StaticQuery, graphql } from "gatsby";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { Modal, Button, Tab, Nav } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Fancybox } from "@fancyapps/ui/src/Fancybox/Fancybox.js";
import "@fancyapps/ui/dist/fancybox.css";
import Download from "../images/download.webp";
import $ from "jquery";

export default function Gallery() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const fnSendMeDetail = (form,pid,pname) => {
    var name = $("#" + form + " input[name=name]").val();
    var email = $("#" + form + " input[name=email]").val();
    var mobile_no = $("#" + form + " input[name=mobile]").val();

    var email_pattern = /^\w+([-+.'][^\s]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
    var mobile_length = mobile_no.length;

    if (name == "") {
      $("#" + form + " .form_error").html("Please enter name");
      return false;
    } else {
      $("#" + form + " .form_error").html("");
    }

    if (email != "") {
      if (!email_pattern.test(email)) {
        $("#" + form + " .form_error").html("Please enter valid email");
        return false;
      } else {
        $("#" + form + " .form_error").html("");
      }
    }

    if (mobile_no == "") {
      $("#" + form + " .form_error").html("Please enter mobile no.");
      return false;
    } else if (mobile_length != 10) {
      $("#" + form + " .form_error").html("Please enter valid mobile no.");
      return false;
    } else {
      $("#" + form + " .form_error").html("");
    }

    $('#' + form + ' button').prop('disabled', true);

    var lead = {
      name: name,
      email: email,
      mobile: mobile_no,
      project: pname,
      project_id: pid
    };

    $.ajax({
      type: "post",
      url: 'https://mls-crm.manishalifespaces.com/api/add_lead',
      data: lead,
      success: function (data, status, jqXHR) {
        window.location.href = 'thanks';
      },
      error: function (jqXHR, status) {
        $('#' + form + ' button').prop('disabled', false);
      }
    });
  }
  return (
    <StaticQuery
      query={graphql`
        query galleryTab {
          allWpCategory(filter: {wpParent: {node: {slug: {eq: "gallery"}}}}) {
            edges {
              node {
                id
                slug
                name
              }
            }
          }
          interior: allWpPost(filter: {terms: {nodes: {elemMatch: {slug: {eq: "interior"}}}}}) {
            edges {
              node {
                id
                title
                featuredImage {
                  node {
                    sourceUrl
                  }
                }
              }
            }
          }
          logo: allWpPost(filter: {uri: {}, slug: {eq: "logo"}}) {
            nodes {
              slug
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          popupLogo:allWpPost(filter: {uri: {}, slug: {eq: "popup-logo"}}) {
            nodes {
              slug
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          popupBgImg:allWpPost(filter: {uri: {}, slug: {eq: "popup-bg-image"}}) {
            nodes {
              slug
              content
              featuredImage {
                node {
                  sourceUrl
                }
              }
            }
          }
          ProjectId:allWpPost(filter: {uri: {}, slug: {eq: "project-id"}}) {
            nodes {
              content
            }
          }
          ProjectName:allWpPost(filter: {uri: {}, slug: {eq: "project-name"}}) {
            nodes {
              content
            }
          }
          primaryColor: allWpPost(filter: {uri: {}, slug: {eq: "primary-color"}}) {
            nodes {
              content
            }
          }
          secondaryColor: allWpPost(filter: {uri: {}, slug: {eq: "secondary-color"}}) {
            nodes {
              content
            }
          }
          sectionBg: allWpPost(filter: {uri: {}, slug: {eq: "section-bg"}}) {
            nodes {
              content
            }
          }
        }
      `}
      render={data => (

        <><section id="gallery" class="bg" style={{ backgroundColor: data.sectionBg.nodes[0].content.replace('<p>','').replace('</p>','') }}>
          <div class="container">
            <div class="header-title">
              <h2 class="text-center" style={{ color: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}>gallery</h2>
              <div class="underline" style={{ borderBottom: `4px solid ${data.secondaryColor.nodes[0].content.replace('<p>','').replace('</p>','')}` }}></div>
            </div>
            <div class="row">
            <Tab.Container id="gallery-tabs" defaultActiveKey="1">
            <Nav variant="pills" className="flex-row">
              <Nav.Item>
                <Nav.Link eventKey="1" href="javascript:void(0);">
                {data.allWpCategory.edges[0].node.name}
                </Nav.Link>
              </Nav.Item>
            </Nav>
            <Tab.Content>
            <Tab.Pane eventKey="1">
              <Slider
                    initialSlide={3}
                    autoplay={true}
                    autoplaySpeed={3000}
                    dots={false}
                    infinite={true}
                    speed={500}
                    slidesToShow={3}
                    slidesToScroll={1}
                    responsive= {[
                      {
                        breakpoint: 1024,
                        settings: {
                          slidesToShow: 2,
                          slidesToScroll: 2,
                          initialSlide:2
                        }
                      },
                      {
                        breakpoint: 600,
                        settings: {
                          slidesToShow: 1,
                          slidesToScroll: 1,
                          initialSlide:1
                        }
                      }
                    ]}>
                    {data.interior.edges.map((galleryimg, index) => (
                      <div class="item"> <a data-fancybox="Gallery" data-src={galleryimg.node.featuredImage.node.sourceUrl} href="javascript:void(0);">
                        <div class="card">
                          <div class="gallery-image" style={{ backgroundImage: `url( ${galleryimg.node.featuredImage.node.sourceUrl})` }}></div>
                          <h5>{galleryimg.node.title}</h5>
                          {/* {console.log(galleryimg)} */}
                        </div>
                      </a> </div>
                    ))}
                  </Slider>
              </Tab.Pane>
              </Tab.Content>
              
            </Tab.Container>
            <div class="text-center mt-5"> <button onClick={handleShow}
                  class="btn btn-primary" style={{ backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}><img src={Download} /> Get
                  Brochure</button> </div>
            </div>
          </div>
        </section><Modal show={show} onHide={handleClose} centered>
        <Modal.Body style={{ backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','') }}>
            <Button variant="primary" onClick={handleClose} style={{ float: 'right', backgroundColor: data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>',''), border: `1px solid ${data.primaryColor.nodes[0].content.replace('<p>','').replace('</p>','')}` }}>X</Button>
              <div class="popup-bg" style={{ backgroundImage: `url( ${data.popupBgImg.nodes[0].featuredImage.node.sourceUrl})` }}>
                <div style={{ backgroundColor: 'rgba(0, 0, 0, 0.61)' }}>
                <img style={{ width: "100%", padding: "4rem 6rem", margin: "auto" }} src={data.popupLogo.nodes[0].featuredImage.node.sourceUrl} />
                </div>
              </div>
              <h4 class="text-center text-white pt-5 pb-4">Fill details for current offers</h4>
              <form id="galleryPopupForm" class="px-4">
                <div class="mb-3"> <input type="text" class="form-control" name="name" placeholder="Name" /> </div>
                <div class="mb-3"> <input type="email" class="form-control" name="email" placeholder="Email" /> </div>
                <div class="mb-3"> <input type="number" class="form-control mobile" name="mobile" placeholder="Mobile No." /> </div>
                {/* <div class="form-check mb-3"> <input class="form-check-input" type="checkbox" value="" id="main-form-check" /> <label class="form-check-label text-white" for="main-form-check"> Yes, I want quick call back </label> </div> */}
                <p class="text-danger form_error"></p><button type="button" class="btn btn-primary shadow-lg w-100 mb-5" onClick={() => fnSendMeDetail("galleryPopupForm",data.ProjectId.nodes[0].content.replace(/<\/?[^>]+(>|$)/g, ""),data.ProjectName.nodes[0].content.replace(/<\/?[^>]+(>|$)/g, ""))} style={{ backgroundColor: data.secondaryColor.nodes[0].content.replace('<p>','').replace('</p>',''), border: `1px solid ${data.secondaryColor.nodes[0].content.replace('<p>','').replace('</p>','')}` }}>SEND ME DETAILS</button> </form>
            </Modal.Body>
          </Modal></>

      )}
    />
  )
}